.form-title {
    text-align: center;
   font-size: 55px;
   margin: 70px 0 0;
}
.form-container {
    padding: 10px;
    max-width: 900px;
    margin: 30px auto 0;
    display: flex;
    flex-direction: column;

   
}

.form-container label {

    
    font-size: 20px;
}

.form-container input,.form-container textarea {

    padding: 10px;
    margin-bottom: 25px;
    font-size: 18px;

}

.form-container textarea {
    font-family: Arial, Helvetica, sans-serif;
    min-height: 200px;
}

.form-container button {
    padding: 15px 0;
    border: 1px solid rgba(0,0,0,0.8);
    background: transparent;
    border-radius: 3px;
    width: 200px;
    cursor: pointer;

}