nav {
    position: fixed;
    top:0;
    width: 100%;
    background: linear-gradient(45deg, rgb(156,14,146),midnightblue);
    height: 50px;
}
.liste {

    list-style-type: none;
    background: linear-gradient(45deg, rgb(156,14,146),midnightblue);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 22px;
}
.items {
    color: rgba(255,255,255,0.8);
    margin: 0 15px;
    cursor: pointer;
    text-transform: uppercase;

}

.btn{

    display: none;
    position: absolute;
    top:5px;
    right:10px;
    padding: 5px;
    font-size: 18px;
    cursor: pointer;
    color: #000;
}

@media screen and (max-width : 500px) {

    .liste {
        flex-direction: column;
        height: auto;
    }

    .items:nth-child(1) {
        border-top: 1px solid rgba(255,255,255,0.8);
        width: 100%;
        margin-top: 70px;
    }

    .items {
        border-bottom: 1px solid rgba(255,255,255,0.8);
        width: 100%;
        line-height: 70px;
        text-align: center;
       
    }

    .btn {
        display: block;
    }
    
}